import React,{ useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import ChangeMemo from './ChangeMemo'
import DeleteMemo from './DeleteMemo'


export default function List() {

    const [memoData, setMemoData] = useState()
    const state = useSelector(state => state)

    console.log(state)
    useEffect(()=>{
        fetch('/memo',{
            method : "GET",
            headers : {"Content-type":"application/json"},
        })
        .then(res => res.json())
        .then(data => setMemoData(data))
    },[state])

    return (
        <div>
            {memoData &&
                <div>
                    {
                        memoData && memoData.map((v)=>(
                            <div key={v.id}>
                                <img src={`img/img${v.id}.png`} alt="" style={{opacity:0.3}}/>
                                <b>{v.title}</b>{v.info}
                                <DeleteMemo id={v.id}/>
                                <ChangeMemo id={v.id}/>
                            </div>
                        ))
                    }
                </div>   
            }
        </div>
    )
}
