import { saveMemo, addState, removeState } from './types'

const init ={data:true, num:100}
function reducer(state = init, action){
    switch(action.type){
        case saveMemo : return {...state, data:!state.data };
        case addState : return {...state,  num:state.num +1};
        case removeState : return {...state, num:state.num -1};
        default : return state;
    }
}
export default reducer;