import React from 'react'
import { useDispatch } from 'react-redux'

export default function ChangeMemo( {id} ) {

    const dispatch = useDispatch()

    function putMemo(e){
        e.preventDefault()
            fetch(`/memo/${id}`,{
                method : "PUT",
                headers : {"Content-Type":"application/json"},
                body : JSON.stringify({title:"ÏàòÏ†ï", info:"Î∂àÍ∞ÄÌï©ÎãàÎã§"})
            })
        dispatch({type:'saveMemo'})
    }
    return (
        <button onClick={putMemo}>
            ÏàòÏ†ï
        </button>
    )
}
