import React ,{ useRef } from 'react'
import List from './List'
import { useDispatch } from "react-redux"
import { saveMemo } from '../redux/types'

export default function Memo() {
  const elInput = useRef([])
  const dispatch = useDispatch()

  function momoPost(e){
      e.preventDefault();

      let titValue = elInput.current[0].value
      let infoValue = elInput.current[1].value

      fetch('/memo',{
          method : "POST",
          headers : {"content-type":"application/json"},
          body : JSON.stringify({title:titValue, info:infoValue})
      })

      dispatch({type:saveMemo})
  }

    return (
        <>
        <div>
            <form name="memo" onSubmit={momoPost}>
                <p>
                    <label>Ï†úÎ™©</label>
                    <input type="text" name="title" placeholder="Ï†úÎ™©ÏùÑÏ†ÅÏñ¥Ï£ºÏÑ∏Ïöî"
                    ref={(el)=>elInput.current[0]=el}></input>
                </p>
                <p>
                    <label>ÎÇ¥Ïö©</label>
                    <textarea name="memo" rows="5" required
                    ref={(el)=>elInput.current[1]=el}></textarea>
                </p>
                <p>
                    <input type="submit" value="Ï†ÄÏû•"/>
                </p>
            </form>
        </div>
        <List/>
        </>
    )
}
