import { useSelector, useDispatch } from 'react-redux'
import { addState, removeState } from "../redux/types";
import Confetti from 'react-confetti'


export default function Home(){
  const {num} = useSelector( (state)=>state );
  const dispatch = useDispatch();

  const confetti = document.querySelector('.confetti')

  function goodClick(){
      dispatch({type:addState})
      confetti.style.display = "block"
      setTimeout(()=> confetti.style.display = "none",3000)
  }

    return(
      <div>
            <Confetti className="confetti" style={{display:"none"}}/>
            <h2> Ï¢ãÏïÑÏöî {num}</h2>
            <button onClick ={ goodClick }> üëç </button>
            <button onClick ={ ()=>dispatch({type:removeState}) }> - </button>
      </div>
    )
} 
