import React from 'react'
import { useDispatch } from 'react-redux'

export default function DeleteMemo( {id} ) {

    const dispatch = useDispatch()

    function delMemo(e){
        e.preventDefault()
            fetch(`/memo/${id}`,{
                method : "DELETE",
                headers : {"Content-Type":"application/json"},
            })

        dispatch({type:'saveMemo'})
    }
    return (
        <button onClick={delMemo}>
            ÏÇ≠Ï†ú
        </button>
    )
}
