import {useState} from "react";
import { ResponsiveBar } from '@nivo/bar'

import Corona from "../db/corona19.json";


export default function Graph(){
    let subject = [];
    let coronaData = Corona.DATA;
    let [data,setData] = useState(Corona.DATA)
    // console.log(Corona.DESCRIPTION); // object;
    // console.log(Corona.DATA); // array;

    for(let tit in Corona.DESCRIPTION){

        // console.log(tit) //Ï†úÎ™©Îì§Ïù¥ Ï∂úÎ†•
        // console.log(Corona.DESCRIPTION[tit]) //Ï†úÎ™©Ïóê Îî∞Î•∏ ÎÇ¥Ïö©Îì§Ïù¥ Ï∂úÎ†•Îê®

        subject.push(Corona.DESCRIPTION[tit]);
    }
    // for(let i of coronaData){
    //     console.log(Object.entries(i)[0][1])
    // }
    function dataChange(e){
        // console.log( window.day.value);
        // console.log( e.target.value);

        setData(
            coronaData.filter(day => (
                day['s_vc_dt'] === e.target.value
            ))
        ); 
    }
    function dataAll(){
        setData(Corona.DATA)
    }

    return (
        <>
            <div style = {{height:'600px'}}>
        <ResponsiveBar
            data={data}
            keys={[ 'fir_inc', 'scd_inc' ]}
            indexBy="s_vc_dt"
            margin={{ top: 50, right: 130, bottom: 50, left: 60 }}
            padding={0.3}
            valueScale={{ type: 'linear' }}
            indexScale={{ type: 'band', round: true }}
            valueFormat={{ format: '', enabled: false }}
            colors={{ scheme: 'nivo' }}
            defs={[
                {
                    id: 'dots',
                    type: 'patternDots',
                    background: 'inherit',
                    color: '#38bcb2',
                    size: 4,
                    padding: 1,
                    stagger: true
                },
                {
                    id: 'lines',
                    type: 'patternLines',
                    background: 'inherit',
                    color: '#eed312',
                    rotation: -45,
                    lineWidth: 6,
                    spacing: 10
                }
            ]}
            fill={[
                {
                    match: {
                        id: 'fries'
                    },
                    id: 'dots'
                },
                {
                    match: {
                        id: 'sandwich'
                    },
                    id: 'lines'
                }
            ]}
            borderColor={{ from: 'color', modifiers: [ [ 'darker', 1.6 ] ] }}
            axisTop={null}
            axisRight={null}
            axisBottom={{
                tickSize: 5,
                tickPadding: 5,
                tickRotation: 0,
                legend: 'ÎÇ†ÏßúÎ≥Ñ ÌòÑÌô©',
                legendPosition: 'middle',
                legendOffset: 32
            }}
            axisLeft={{
                tickSize: 5,
                tickPadding: 5,
                tickRotation: 0,
                legend: 'ÏΩîÎ°úÎÇò Ï†ëÏ¢ÖÎ•†',
                legendPosition: 'middle',
                legendOffset: -40
            }}
            labelSkipWidth={12}
            labelSkipHeight={12}
            labelTextColor={{ from: 'color', modifiers: [ [ 'darker', 1.6 ] ] }}
            legends={[
                {
                    dataFrom: 'keys',
                    anchor: 'bottom-right',
                    direction: 'column',
                    justify: false,
                    translateX: 120,
                    translateY: 0,
                    itemsSpacing: 2,
                    itemWidth: 100,
                    itemHeight: 20,
                    itemDirection: 'left-to-right',
                    itemOpacity: 0.85,
                    symbolSize: 20,
                    effects: [
                        {
                            on: 'hover',
                            style: {
                                itemOpacity: 1
                            }
                        }
                    ]
                }
            ]}
        />
            </div>
            <section>
                <h2> ÏΩîÎ°úÎÇò Î∞±Ïã† Ï†ëÏ¢ÖÏûê ÌòÑÌô©</h2>
                <div>
                    <select id="day" onChange={dataChange}>
                        {
                            coronaData.map(function(data,k){
                                return(
                                    <option key={k}>{data['s_vc_dt']}</option>
                                )
                            })
                        }
                    </select>
                    <button onClick={dataAll}>Ï†ÑÏ≤¥Î≥¥Í∏∞</button>
                </div>

                <table border="1">
                    <thead>
                        <tr>
                            {
                                subject.map(function(getTit,k){
                                    return (<th key={k}>{getTit}</th>)
                                })
                            }
                        </tr>
                    </thead>
                    <tbody>
                        {
                            data.map(function(getdata,k){
                                return (
                                <tr key={k}>
                                    {/* <td>{data['scd_inc1']}</td> */}
                                    <td>{getdata.scd_inc1}</td>
                                    <td>{getdata.fir_inc1}</td>
                                    <td>{getdata.scd_inc}</td>
                                    <td>{getdata.s_vc_dt}</td>
                                    <td>{getdata.fir_inc}</td>
                                    <td>{getdata.fir_sub}</td>
                                    <td>{getdata.fir_inc_rate}</td>
                                    <td>{getdata.scd_inc_rate}</td>
                                    {}
                                </tr>
                                )
                            })
                        }
                    </tbody>
                </table>
            </section>
        </>
    )
}